<template>
  <div class="home">
    <div class="content p-1 d-flex justify-content-center align-items-center">
      <img src="../assets/images/backarrow.png" class="position-absolute back-arrow" width="35px" alt="" @click="$router.go(-1)">
      <div class="w-85">
        <!-- 結果畫面 -->
        <div v-if="answer">
          <p class="h8 font-weight-bold text-white">正確率:{{Math.floor(((testlist.length-wronganslist.length) / testlist.length)*100)}}%</p>
          <div class="table-frame p-2 mt-2">
            <table class="table">
              <thead class="font-weight-bold h5">
                <tr>
                  <th scope="col">題目</th>
                  <th scope="col">正確答案</th>
                  <th scope="col">您的答案</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in wronganslist" :key="index">
                  <th scope="row">{{item.ques}}</th>
                  <td>{{item.correct}}</td>
                  <td>{{item.yours}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="p-3 border border-white text-white w-50 mx-auto mt-3" @click="$router.push({name:'Home'})">回首頁</div>
        </div>
        <!-- 結果畫面 end-->
        <!-- 點擊查看結果畫面 -->
        <div class="p-3 border border-white text-white w-50 mx-auto" v-if="finished" @click="showanswer">看結果</div>
        <!-- 按開始時畫面 -->
        <div v-if="start">
          <select name="" v-model="checkedType" id="" class="tab-btn text-first font-weight-bold p-1 h7 d-flex align-items-center text-nowrap mx-auto">
            <option value="">全部</option>
            <option value="location">方位;位置</option>
            <option value="time">時間;時候</option>
            <option value="title">稱謂;職稱</option>
          </select>
          <div class="p-3 border border-white text-white w-50 mx-auto mt-3" @click="teststart">開始</div>
        </div>
        <!-- 按開始時畫面 end -->
        <!-- 題目 -->
        <div v-if="topic">
          <p class="h8 text-white">{{testlist[testnum].ques}}</p>
          <div class="d-flex flex-wrap mt-5 text-first">
            <div class="h7 py-5 test-card bg-white mr-2" @click="submit({value:0})">{{testlist[testnum].ans[0]}}</div>
            <div class="h7 py-5 test-card bg-white" @click="submit({value:1})">{{testlist[testnum].ans[1]}}</div>
            <div class="h7 py-5 test-card bg-white mr-2 mt-2" @click="submit({value:2})">{{testlist[testnum].ans[2]}}</div>
            <div class="h7 py-5 test-card bg-white mt-2" @click="submit({value:3})">{{testlist[testnum].ans[3]}}</div>
          </div>
        </div>
        <!-- 題目end -->
      </div>
    </div>
    <!-- model -->
    <div v-if="notice" class="model d-flex justify-content-center align-items-center">
      <div>
        <div class="border p-3 bg-white">
          <div class="model-title border-bottom p-2">notice</div>
          <div class="model-content mt-3">
            <p>the task already finished</p>
          </div>
        </div>
        <div class="mt-2">
          <button @click="notice = false">back</button>
          <button>confirm</button>
        </div>
      </div>
    </div>
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>
  </div>
</template>
<script>
import $ from 'jquery'
import Loading from '../components/Loading'
export default {
  name: 'Test',
  components: {
    Loading
  },
  data () {
    return {
      isLoading: false,
      type: 'all',
      show: false,
      rewardType: 'ufun',
      testlist: [],
      topiclist: [],
      testnum: 0,
      swiperOptions: {
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      },
      answerlist: [],
      ansarr: [],
      notice: false,
      start: true,
      topic: false,
      finished: false,
      answer: false,
      wronganslist: [],
      checkedType: '',
      apiURL: 'https://script.google.com/macros/s/AKfycbxiFyDKihL6_LCHsOrPt3EmyvESvE0sc_Urjsw0jz47OznzUcBwLysqo7piCDF3tNLO/exec'
    }
  },
  created () {
    this.getlist()
  },
  mounted () {
  },
  watch: {
  },
  methods: {
    submit (item) {
      this.testnum++
      this.answerlist.push(item.value)
      if (this.answerlist.length === this.testlist.length) {
        this.topic = false
        this.finished = true
      }
      console.log('this.answerlist', this.answerlist)
    },
    teststart () {
      this.test()
      this.start = false
      this.topic = true
    },
    showanswer () {
      this.wronganslist = []
      for (let z = 0; z < this.ansarr.length; z++) {
        if (this.ansarr[z] !== this.answerlist[z]) {
          this.wronganslist.push({num: z, ques: this.topiclist[z].word, correct: this.topiclist[z].desc, yours: this.testlist[z].ans[this.answerlist[z]]})
        }
      }
      this.finished = false
      this.answer = true
    },
    test () {
      if (this.checkedType !== '') {
        this.topiclist = this.topiclist.filter(item =>
          item.type === this.checkedType
        )
      }
      this.topiclist = this.topiclist.sort(() => Math.random() - 0.5)
      let test11 = []
      for (let i = 0; i < this.topiclist.length; i++) {
        let ansval = Math.floor(Math.random() * (4))
        this.ansarr.push(ansval)
        test11.push({ques: this.topiclist[i].word, ans: ['', '', '', '']})
        test11[i].ans[ansval] = this.topiclist[i].desc
        let threenum = []
        let allarray = []
        for (let b = 0; b < this.topiclist.length; b++) {
          allarray.push(b)
        }
        for (let a = 0; a < 3; a++) {
          let z = Math.floor(Math.random() * (this.topiclist.length - a))
          do {
            z = Math.floor(Math.random() * (this.topiclist.length - a))
          } while (z === i)
          threenum.push(allarray[z])
          allarray.splice(z, 1)
        }
        let z = 0
        console.log('test11', test11[i], threenum)
        for (let a = 0; a < 4; a++) {
          if (test11[i].ans[a] === '') {
            console.log(a)
            test11[i].ans[a] = this.topiclist[threenum[z]].desc
            z++
          }
        }
      }
      this.testlist = test11
      console.log(this.testlist)
    },
    getlist () {
      const vh = this
      vh.isLoading = true
      $.ajax({
        type: 'GET',
        // url: '../static/word.json',
        url: vh.apiURL,
        // processData: false, // Important!
        // contentType: false,
        cache: false,
        crossDomain: true,
        /* 跨域請求 */
        // crossDomain: true,
        xhrFields: {
          // withCredentials: true // 跨域請求是否附帶 Cookies/HTTP Auth/Client SSL, xhr.withCredentials = true, Server 設定 Access-Control-Allow-Origin
        },
        data: {
          // boxToken: vh.boxToken,
          // page: vh.currentPage
        },
        timeout: 600000,
        dataType: 'json',
        success: function (response) {
          if (!response.error) {
            console.log(response)
            vh.topiclist = response.data
            console.log(vh.topiclist)
            vh.isLoading = false
            console.log(vh.topiclist)
            // Math.floor(Math.random() * (response.data.length))
          } else {
            // alert('發生錯誤：' + response.error.msg)
            // vh.errorMsg = response.error.msg
            // vh.errorModal = true
            // $("#btn-new-game").attr('disabled', false);
          }
        },
        error: function (jqXHR) {
          // alert('發生錯誤：' + jqXHR.status)
          // $("#btn-new-game").attr('disabled', false)
        }
      })
    }
  }
}
</script>

<style>
  .home{
    background-color:#ffbebe !important;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    /* height: 100%; */
    /* -webkit-background-size: cover ; */
    /* background-attachment: fixed; */
    }
  .content{
    height: 100vh;
    width: 100%;
    position: absolute;
    top: 0;
  }
  .btn-group{
    position: absolute;
    bottom: 20px;
  }
  .tab-btn{
    /* border-bottom: 2px solid gray; */
    border-radius: 5px;
    border: 1px solid gray;
    padding: 0 5px 0 5px;
  }
  .btn_checked{
    background: rgb(230, 208, 208);
  }
  .check-item{
    border: 1px solid gray;
    width: 25px;
    height: 25px;
    border-radius: 50%;
  }
  .progress-text{
    transform: translateY(6px);
  }
  .item-checked{
    border-bottom: 5px solid rgb(255, 255, 255);
    border-right: 5px solid rgb(255, 255, 255);
    height: 25px;
    width: 13px;
    transform: rotate(45deg) translate(0px, -10px);
  }
  .model{
    background-color: rgba(0,0,0,0.5) !important;
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
  }
  .test-card{
    width: calc(50% - 4px);
    border-radius: 15px;
  }
  .table-frame{
    max-height: 70vh;
    overflow: scroll;
    background: rgb(255, 255, 255);
    border-radius: 15px;
  }
</style>



