<template>
  <div class="home">
    <div class="content p-1 d-flex justify-content-center align-items-center">
      <div class="w-50">
        <div class="p-3 border border-white mb-3" @click="$router.push({name: 'Contentlist'})">複習單詞</div>
        <div class="p-3 border border-white mb-3" @click="$router.push({name: 'Test'})">測驗</div>
        <div class="p-3 border border-white" @click="$router.push({name: 'Content'})">編輯單詞</div>
      </div>
    </div>
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>
  </div>
</template>
<script>
// import $ from 'jquery'
import Loading from '../components/Loading'
export default {
  name: 'Home',
  components: {
    Loading
  },
  data () {
    return {
      isLoading: false,
      type: 'all',
      show: false,
      swiperOptions: {
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      }
    }
  },
  created () {
    // this.get()
  },
  mounted () {
  },
  watch: {
  },
  methods: {
  }
}
</script>

<style>
  .home{
    background-color:#ffbebe !important;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    /* height: 100%; */
    /* -webkit-background-size: cover ; */
    /* background-attachment: fixed; */
    }
  .content{
    height: 100vh;
    width: 100%;
    position: absolute;
    top: 0;
    color: white;
  }
  .btn-group{
    position: absolute;
    bottom: 20px;
  }
  .tab-btn{
    /* border-bottom: 2px solid gray; */
    border-radius: 5px;
    border: 1px solid gray;
    padding: 0 5px 0 5px;
  }
  .btn_checked{
    background: rgb(230, 208, 208);
  }
  .check-item{
    border: 1px solid gray;
    width: 25px;
    height: 25px;
    border-radius: 50%;
  }
  .progress-text{
    transform: translateY(6px);
  }
  .item-checked{
    border-bottom: 5px solid rgb(255, 255, 255);
    border-right: 5px solid rgb(255, 255, 255);
    height: 25px;
    width: 13px;
    transform: rotate(45deg) translate(0px, -10px);
  }
  .model{
    background-color: rgba(0,0,0,0.5) !important;
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
  }
</style>



