<template>
<div class="loading">
  <div class="d-flex h-100 justify-content-center align-items-center">
    <div class="position-relative">
      <div class="circle position-absolute"></div>
      <div class="circle position-absolute"></div>
      <div class="circle position-absolute"></div>
      <div class="circle position-absolute"></div>
      <div class="circle position-absolute"></div>
      <div class="circle position-absolute"></div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'Loading'
}
</script>

<style>
.loading {
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.5);
  /* background: url('../assets/img/bg_dark.png') no-repeat; */
  width: 100vw;
  height: 100vh;
  z-index: 1000;
}
.circle{
  width: 3px;
  height: 10px;
  border-radius: 35%;
  background: white;
  animation: dot 0.6s infinite;
}
.circle:nth-child(1){
  animation-delay:0.1s;
  transform-origin: top;
  transform: rotate(0deg) translateY(2px);
}
.circle:nth-child(2){
  animation-delay:0.2s;
  transform-origin: top;
  transform: rotate(60deg) translate(-1.5px, 4.5px);
}
.circle:nth-child(3){
  animation-delay:0.3s;
  transform-origin: top;
  transform: rotate(120deg) translate(-1.5px, 6px);
}
.circle:nth-child(4){
  animation-delay:0.4s;
  transform-origin: top;
  transform: rotate(180deg) translateY(6px);
}
.circle:nth-child(5){
  animation-delay:0.5s;
  transform-origin: top;
  transform: rotate(240deg) translate(1.5px, 6px);
}
.circle:nth-child(6){
  animation-delay:0.6s;
  transform-origin: top;
  transform: rotate(300deg) translate(1.5px, 4.5px);
}
</style>



