<template>
  <div class="home">
    <div class="content p-1 d-flex justify-content-center align-items-center">
      <img src="../assets/images/backarrow.png" class="position-absolute back-arrow" width="35px" alt="" @click="$router.go(-1)">
      <div @click="page({type: 'sub'})" class="mr-1 text-white cursor-point">
        <img src="../assets/images/arrow_left.png" width="35px" alt="">
      </div>
      <div class="w-75">
        <div class="d-flex justify-content-around mb-2">
          <select name="" class="tab-btn text-first font-weight-bold p-2 h7 d-flex align-items-center text-nowrap" v-model="checkedType" id="">
            <option value="">全部</option>
            <option value="location">方位;位置</option>
            <option value="time">時間;時候</option>
            <option value="title">稱謂;職稱</option>
          </select>
        </div>
        <div class="position-relative listcard d-flex align-items-center justify-content-center bg-white p-4 mt-3">
          <!-- <div v-if="cardList.length >= 1" class="position-absolute del-icon" @click="delCard">-</div> -->
          <div v-if="cardList.length < 1" class="text-first">add something</div>
          <div v-else class="">
            <div class="w-100 p-3">
              <p class="h8 mt-3 text-first">{{ cardList[cardnum].chineseword }}</p>
              <p class="h6 text-first">({{ cardList[cardnum].word }})</p>
              <p class="h7 mt-3 text-second">{{ cardList[cardnum].desc }}</p>
              <div class="mt-3 h6 text-third" v-html="cardList[cardnum].example"></div>
            </div>
          </div>
        </div>
        <!-- <div @click="gototest" class="tab-btn mt-2 text-white font-weight-bold p-2 h7 d-flex align-items-center text-nowrap">測驗</div> -->
      </div>
      <div @click="page({type: 'add'})" class="ml-1 text-white cursor-point">
        <img src="../assets/images/arrow_right.png" width="35px" alt="">
      </div>
    </div>
    <!-- model -->
    <div v-if="notice" class="model d-flex justify-content-center align-items-center">
      <div>
        <div class="border p-3 bg-white">
          <div class="model-title border-bottom p-2">notice</div>
          <div class="model-content mt-3">
            <p>the task already finished</p>
          </div>
        </div>
        <div class="mt-2">
          <button @click="notice = false">back</button>
          <button>confirm</button>
        </div>
      </div>
    </div>
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>
  </div>
</template>
<script>
import $ from 'jquery'
import Loading from '../components/Loading'
export default {
  name: 'Home',
  components: {
    Loading
  },
  data () {
    return {
      isLoading: false,
      type: 'all',
      show: false,
      rewardType: 'ufun',
      cardnum: 0,
      cardList: [],
      allList: [],
      notice: false,
      carditem: {
        word: '',
        chineseword: '',
        example: '',
        desc: ''
      },
      cardH: '',
      cardW: '',
      checkedType: '',
      apiURL: 'https://script.google.com/macros/s/AKfycbxiFyDKihL6_LCHsOrPt3EmyvESvE0sc_Urjsw0jz47OznzUcBwLysqo7piCDF3tNLO/exec'
    }
  },
  created () {
    // this.get()
    this.getlist()
    this.$nextTick(() => {
      this.getlist()
      this.cardH = document.querySelector('.addcard').offsetHeight
      console.log(this.cardH)
    })
  },
  mounted () {
  },
  watch: {
    type () {
      console.log(this.cardH)
      if (this.type === 'new') {
        this.$nextTick(() => {
          document.querySelector('.listcard').style.height = this.cardH + 'px'
        })
      }
    },
    checkedType () {
      if (this.checkedType !== '') {
        this.cardList = this.allList.filter(item =>
          item.type === this.checkedType
        )
      } else {
        this.cardList = this.allList
      }
      this.cardnum = 0
    }
  },
  methods: {
    page (item) {
      console.log(this.cardList)
      if (item.type === 'sub') {
        if (this.cardnum === 0) return
        this.cardnum--
      } else {
        if (this.cardnum === this.cardList.length - 1) return
        this.cardnum++
      }
      // if (this.cardnum === 0 || this.cardnum === this.cardList.length) return
    },
    // delCard () {
    //   this.cardList.splice(this.cardnum, 1)
    //   console.log(this.cardList)
    // },
    getlist () {
      console.log('get')
      const vh = this
      vh.isLoading = true
      $.ajax({
        type: 'GET',
        url: vh.apiURL,
        // processData: false, // Important!
        // contentType: false,
        cache: false,
        crossDomain: true,
        /* 跨域請求 */
        // crossDomain: true,
        // xhrFields: {
        // withCredentials: true // 跨域請求是否附帶 Cookies/HTTP Auth/Client SSL, xhr.withCredentials = true, Server 設定 Access-Control-Allow-Origin
        // },
        data: {},
        timeout: 600000,
        dataType: 'json',
        success: function (response) {
          if (!response.error) {
            console.log(response)
            // console.log(typeof response[1])
            vh.allList = response.data
            vh.cardList = vh.allList
            vh.isLoading = false
            console.log(vh.allList, vh.allList.length)
            console.log(vh.cardList, vh.cardList.length)
          } else {
            // alert('發生錯誤：' + response.error.msg)
            // vh.errorMsg = response.error.msg
            // vh.errorModal = true
            // $("#btn-new-game").attr('disabled', false);
          }
        },
        error: function (jqXHR) {
          // alert('發生錯誤：' + jqXHR.status)
          // $("#btn-new-game").attr('disabled', false)
        }
      })
    }
  }
}
</script>

<style>
  .home{
    background-color:#ffbebe !important;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    /* height: 100%; */
    /* -webkit-background-size: cover ; */
    /* background-attachment: fixed; */
    }
  .addcard{
    border-radius: 15px;
  }
  .listcard{
    border-radius: 15px;
    height: 75vh;
  }
  .addcard input{
    outline: none;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid #ffbebe;
    color: #ffbebe;
    font-size: 18px;
  }
  .addcard input:nth-child(1){
    font-size: 28px;
  }
  .addcard textarea{
    border: 1px solid #ffbebe;
    outline: none;
    color: #ffbebe;
    font-size: 18px;

  }
  .content{
    height: 100vh;
    width: 100%;
    position: absolute;
    top: 0;
  }
  .btn-group{
    position: absolute;
    bottom: 20px;
  }
  .add-btn{
    background: #ffbebe;
    /* border: 2px solid #ffffff; */
    border-radius: 8px;
    color: #ffffff;
    font-weight: 700;
    width: fit-content;
  }
  .tab-btn{
    /* border-bottom: 2px solid gray; */
    border-radius: 5px;
    border: 1px solid rgb(255, 255, 255);
  }
  .btn_checked{
    background: rgb(246, 246, 246);
    color: #ffbebe !important;
  }
  /* .list-item{
    border: 1px rgb(112, 111, 111) solid;
    border-radius: 5px;
  } */
  .check-item{
    border: 1px solid gray;
    width: 25px;
    height: 25px;
    border-radius: 50%;
  }
  .progress-text{
    transform: translateY(6px);
  }
  .item-checked{
    border-bottom: 5px solid rgb(255, 255, 255);
    border-right: 5px solid rgb(255, 255, 255);
    height: 25px;
    width: 13px;
    transform: rotate(45deg) translate(0px, -10px);
  }
  .model{
    background-color: rgba(0,0,0,0.5) !important;
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
  }
  .del-icon{
    width: 25px;
    height: 25px;
    border-radius:50%;
    background: rgb(253, 115, 115);
    color: white;
    font-weight: 900;
    top: -8px;
    right: -8px;
    font-size: 20px;
  }
</style>



